import classes from "./Card.module.css";

const Card = (props) => {
  return (
    <div className={classes.container}>
      <div className={classes.card}>
        <div className={classes.imgBx}></div>
        <div className={classes.contentBx}>
          <h2 className={classes.heading}>Global Citizenship Education</h2>
          <div className={classes.content2}>
            <h3>
              We are one of the pioneering international organizations
              established with a mandate to advance United Nations' Sustainable
              Development Goal 4.7, that is, Global Citizenship Education.
            </h3>
            <a href="https://google.com">Some Link</a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Card;
