import { Link } from "react-router-dom";
import GeneralContainer from "../GeneralContainer/GeneralContainer";
import classes from "./Footer.module.css";
const Footer = (props) => {
  return (
    <GeneralContainer>
      <div className={classes.footer}>
        <div className={classes.form__and__logo}>
          <div>
            <h2>LOGO</h2>
          </div>
          <div>
            <h4>CONTACT</h4>
            <div className={classes.contact_form}>
              <div>
                <input
                  placeholder="First Name"
                  className={classes.form__input}
                />
                <input
                  placeholder="Last Name"
                  className={classes.form__input}
                />
              </div>
              <div>
                <input
                  placeholder="Email"
                  className={classes.form__input__email}
                />
                <a className={classes.form__button} href="/">
                  Submit
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className={classes.container}>
          <div>
            <h5>DISCOVER</h5>
            <Link>About the Foundation</Link>
            <Link>Secretariat</Link>
            <Link>GCED e-Campus</Link>
            <Link>Impact 500 Organizations</Link>
          </div>
          <div>
            <h5>DISCOVER</h5>
            <Link>About the Foundation</Link>
            <Link>Secretariat</Link>
            <Link>GCED e-Campus</Link>
            <Link>Impact 500 Organizations</Link>
          </div>
          <div>
            <h5>DISCOVER</h5>
            <Link>About the Foundation</Link>
            <Link>Secretariat</Link>
            <Link>GCED e-Campus</Link>
            <Link>Impact 500 Organizations</Link>
          </div>
          <div>
            <h5>DISCOVER</h5>
            <Link>About the Foundation</Link>
            <Link>Secretariat</Link>
            <Link>GCED e-Campus</Link>
            <Link>Impact 500 Organizations</Link>
          </div>
        </div>
        <div className={classes.lastline}>
          <h5> 2022, Startup Story Private Limited. All rights reserved</h5>
        </div>
      </div>
    </GeneralContainer>
  );
};

export default Footer;
